.container {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.forms-wrapper {
  width: 980px;
}

.forms-wrapper h1{
  text-align: start;
  letter-spacing: -1px;
  font-size: 60px;
}

form{
  width: 100%;
  margin-top: 5px;
}

.textInputWrapper {
  position: relative;
  width: 380px;
  margin: 12px 5px;
  --accent-color: #ff04fb;
}

.textInputWrapper:before {
  transition: border-bottom-color 200ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  border-bottom: 1px solid rgba(0, 0, 0, 0.42);
}

.textInputWrapper:before,
.textInputWrapper:after {
  content: "";
  left: 0;
  right: 0;
  position: absolute;
  pointer-events: none;
  bottom: -1px;
  z-index: 4;
  width: 100%;
}

.textInputWrapper:focus-within:before {
  border-bottom: 1px solid var(--accent-color);
}

.textInputWrapper:before {
  transition: border-bottom-color 200ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  border-bottom: 1px solid rgba(0, 0, 0, 0.42);
}

.textInputWrapper:focus-within:before {
  border-bottom: 1px solid var(--accent-color);
  transform: scaleX(1);
}

.textInputWrapper:focus-within:after {
  border-bottom: 2px solid var(--accent-color);
  transform: scaleX(1);
}

.textInputWrapper:after {
  content: "";
  transform: scaleX(0);
  transition: transform 250ms cubic-bezier(0, 0, 0.2, 1) 0ms;
  will-change: transform;
  border-bottom: 2px solid var(--accent-color);
  border-bottom-color: var(--accent-color);
}

.textInput::placeholder {
  transition: opacity 250ms cubic-bezier(0, 0, 0.2, 1) 0ms;
  opacity: 1;
  user-select: none;
  color: rgba(255, 255, 255, 0.582);
}

.textInputWrapper .textInput {
  border-radius: 5px 5px 0px 0px;
  box-shadow: 0px 2px 5px rgb(35 35 35 / 30%);
  max-height: 36px;
  background-color: #13121a;
  transition-timing-function: cubic-bezier(0.25, 0.8, 0.25, 1);
  transition-duration: 200ms;
  transition-property: background-color;
  color: #e8e8e8;
  font-size: 14px;
  font-weight: 500;
  padding: 12px;
  width: 400px;
  border-left: none;
  border-bottom: none;
  border-right: none;
  font-family: "Poppins";
}
.textInputWrapper .my-textarea {
  width: 400px;
  height: 150px;
  max-height: 400px;
  max-width: 400px;
  min-height: 36px;
  min-width: 400px;
}
.textInputWrapper .firstInput {
  width: 196px;
  font-family: "Poppins";
}
.textInputWrapper .textInput:focus,
.textInputWrapper .textInput:active {
  outline: none;
}

.textInputWrapper:focus-within .textInput,
.textInputWrapper .textInput:focus,
.textInputWrapper .textInput:active {
  background-color: #0b0a14;
 
}

.textInputWrapper:focus-within .textInput::placeholder {
  opacity: 0;
}

.firstFormRow {
  width: 410px !important; 
  display: flex;
}
.second-row{
  margin-top: -1px;
}
textarea {
  width: 250px;
  height: 300px;
  max-height: 400px;
  font-family: "Poppins";
  border-top: 1px solid rgb(96, 96, 96);
}


h1{
  color: #ff04fb;
  font-family: "Goldman", sans-serif;
  font-size: 60px;
  font-weight: 500;

}

.inputButton{
  width: 400px;
  height: 35px;
  margin-top: 20px;
  margin-left: 6px;
  background: transparent;
  border: #ff04fb 3px solid;
  color: white;
  border-radius: 11px;
  font-size: 15px;
}

.inputButton:hover {
  background-color: rgb(255, 4, 251);
  cursor: pointer;
}


@media only screen and (min-width: 1800px){
  .container{
    margin-left: -190px;
  }
}